// Logistics App - Initial Structure
import React, { useState } from 'react';
import './App.css';

// Sidebar Component
interface SidebarProps {
    items: string[];
    onSelect: (item: string) => void;
}

function Sidebar({ items, onSelect }: SidebarProps) {
    return (
        <div className="sidebar">
            <h2>Navigation</h2>
            <ul>
                {items.map((item, index) => (
                    <li key={index} onClick={() => onSelect(item)}>{item}</li>
                ))}
            </ul>
        </div>
    );
}

// Dashboard Component
function Dashboard() {
    return (
        <div className="dashboard">
            <h1>Welcome to Logistics App</h1>
            <p>Your orders and analytics will appear here.</p>
        </div>
    );
}

// Order Management Component
function Orders() {
    const [orders, setOrders] = useState([
        { id: 1, name: 'Order 1', status: 'Delivered' },
        { id: 2, name: 'Order 2', status: 'Pending' },
        { id: 3, name: 'Order 3', status: 'In-Transit' },
    ]);

    return (
        <div className="orders">
            <h1>Order Management</h1>
            <table>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Name</th>
                        <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                    {orders.map(order => (
                        <tr key={order.id}>
                            <td>{order.id}</td>
                            <td>{order.name}</td>
                            <td>{order.status}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

// Main App Component
function App() {
    const [activePage, setActivePage] = useState('Dashboard');

    const pages = {
        Dashboard: <Dashboard />,
        Orders: <Orders />,
    };

    return (
        <div className="app">
            <Sidebar 
                items={Object.keys(pages)} 
                onSelect={setActivePage} 
            />
            <main>
                {pages[activePage]}
            </main>
        </div>
    );
}

export default App;
